/* google fonts import */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

/* basic for all pages in all browsers */
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* some variables */
:root {
    --color-backgrnd: #1f1f38 ;
    --color-backgrnd-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-large-device: 75%;
    --container-width-medium-device: 86%;
    --container-width-small-device: 90%;
}

html {
    scroll-behavior: smooth; 
}

/* remove scrollbar?
::-webkit-scrollbar {
    display: none;
} */

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-backgrnd);
    color: var(--color-white);
    line-hight: 1.7;
    background-image: url(../src/assets/violett.jpg);
}

.container {
    width: var(--container-width-large);
    padding: 2rem;
    margin: 0 auto;

    /* display: flex;
    justify-content: center;
    align-items: center; */

    /* display: grid;
    place-items: center; */
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition); 
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-backgrnd);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-backgrnd);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-medium);
    }

    section {
        margin-top: 6rem;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-small);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
