header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header__container h2 {
    color: var(--color-primary);
}

/* CTA */
.cta {
    margin-top: 2.5rem;
    display: flex; /* so I can make a space between the buttons */
    gap: 1.2rem;
    justify-content: center;
}

/* HeaderSocials */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* profile image */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5.5rem 1.5rem 1.5rem 1.5rem;
}

/* scroll down */
.scroll__down {
    position: absolute;
    right: -1rem;
    bottom: 6rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERY FOR TABLETS (MEDIUM) */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* MEDIA QUERY FOR SMARTPHONES (SMALL) */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }
}
